import React from '../index'
import Banner from './components/Banner'
import Nav from './components/Nav'
import Content from './components/Content'
import Demo from './components/Demo'
import Footer from './components/Footer'
import './App.less'

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      fix: false,
    }
  }
  componentDidMount() {
    window.addEventListener('scroll', e => {
      if (window.pageYOffset > 400 && !this.state.fix) {
        this.setState({ fix: true })
      } else if (window.pageYOffset <= 400 && this.state.fix) {
        this.setState({ fix: false })
      }
    })
  }
  render() {
    return (
      <div className="App">
        <Banner/>
        <Nav fix={this.state.fix}>
          <a className={{ tab: true, color: this.state.fix }} href="#intro"></a>
          <a className={{ tab: true, color: this.state.fix }} href="#demo"></a>
          <a className={{ tab: true, color: this.state.fix }} href="#docu"></a>
        </Nav>
        <Content fix={this.state.fix}>
          <div id="intro" className="anchor" />
          <div className="item">
            <h1 class="title"></h1>
            <p class="desc indent">KutReact-LikeReactKutcreateElementrenderComponentKut</p>
            <p class="desc indent">()ReactKutReactKutdiffReactdiffdiff<a href="https://zhuanlan.zhihu.com/p/20346379"></a>ReactdiffKutdiffdiffdiff</p>
            <p class="desc indent">ReactFiberKutContextPortalpull requeststar</p>
          </div>
          <div id="demo" className="anchor" />
          <div className="item">
            <h1 class="title"></h1>
            <Demo />
          </div>
          <div id="docu" className="anchor" />
          <div className="item">
            <h1 class="title"></h1>
            <p class="desc indent">KutTypeScript.d.tsVSCode()React</p>
            <h2 class="title">Kut.Component</h2>
            <h2 class="title">Kut.createElement</h2>
            <h2 class="title">Kut.render</h2>
          </div>
        </Content>
        <Footer />
      </div>
    )
  }
}

export default App
