import React from '../../../index'
import './Demo.less'

let uniqueKey = 0
let holding = false
let pos = { x: 0, y: 0 }

class Demo extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      input: '',
      todos: [
        { key: -1, item: 'React' },
        { key: -2, item: '' },
        { key: -3, item: '' },
        { key: -4, item: '' },
        { key: -5, item: '' },
      ],
    }
  }
  handleInput(e) {
    this.setState({ input: e.target.value })
  }
  handleAdd(e) {
    if (this.state.input !== '') {
      const todo = { key: uniqueKey++, item: this.state.input }
      const todos = [ ...this.state.todos ]
      todos.push(todo)
      this.setState({ todos, input: '' })
    }
  }
  handleRan(index) {
    const todos = [ ...this.state.todos ]
    const shuffleTodos = []
    while(todos.length) {
      const i = Math.floor(Math.random() * todos.length)
      shuffleTodos.push(todos[i])
      todos.splice(i, 1)
    }
    this.setState({ todos: shuffleTodos })
  }
  handleDel(index) {
    const todos = [ ...this.state.todos ]
    todos.splice(index, 1)
    this.setState({ todos })
  }
  handleHold(e) {
    if (e.target.tagName.toLowerCase() === 'li') {
      holding = true
      pos.x = e.x
      pos.y = e.y
      e.target.style.position = 'relative'
    }
  }
  handleMove(e) {
    if (holding) {
      e.target.style.left = e.x - pos.x
      e.target.style.top = e.y - pos.y
      console.log(e.x, e.y)
    }
  }
  handleRelease(e) {
    e.target.style.position = ''
    e.target.style.left = 0
    e.target.style.top = 0
    holding = false
  }
  render() {
    return (
      <div className="Demo">
        <p class="desc"></p>
        <div class="input-box">
          <input class="input" onInput={this.handleInput.bind(this)} value={this.state.input}></input>
          <button class="add" onClick={this.handleAdd.bind(this)}></button>
          <button class="add" onClick={this.handleRan.bind(this)}></button>
        </div>
        <ul class="list">
          {
            this.state.todos.length
            ? this.state.todos.map((todo, index) => 
              <li class="item" key={todo.key}
                style={{left: '50px'}}
                onMouseDown={this.handleHold.bind(this)}
                onMouseMove={this.handleMove.bind(this)}
                onMouseUp={this.handleRelease.bind(this)}
              >
                {todo.item}
                <button class="del" onClick={() => this.handleDel(index)}></button>
              </li>
            )
            : <li class="item"></li>
          }
        </ul>
      </div>
    )
  }
}

export default Demo
